<template>
  <div class="welcome-detail">
      {{ msg }} {{ pid }}
  </div>
</template>
<script>
    export default {
      name: 'detail',
      data () {
        return {
          msg: 'this is detail page,fuuuuuuck'
        }
      },
      computed: {
        'pid' () {
          return this.$route.params.id
        }
      }
    }
</script>


